/*
----------------------------
Common classes
----------------------------
*/
.bold {
    font-weight: bold;
}
.black {
    color: #000000;
}
.green {
    color: #8cb46e;
}
.orange {
    color: #eba550;
}
.red {
    color: #9f1924;
}
.blue {
    color: #007a93 !important;
}
.font-size-90 {
    font-size: 0.9rem;
}
.font-size-80 {
    font-size: 0.8rem;
}
.padding-small-vertical {
    padding: 10px 0;
}
.padding-no {
    padding: 0px 0;
}

/*
----------------------------
Common toolbar
----------------------------
*/
.toolbar-btn-container {
    margin-top: 10px;
}
.toolbar-btn-container > button {
    margin-right: 10px;
}

/*
----------------------------
Context menu
----------------------------
*/
.context-menu {
    width: 150px;
    z-index: 100;
    position: absolute;
}

/*
----------------------------
App
----------------------------
*/
.app {
    text-align: left;
    background-color: #ffffff;

    /* Prevent the horizontal scrollbar if a vertical scrollbar is needed (see CEBAS-1637) */
    overflow-x: hidden;
}
.content {
    padding: 1vmin 10vmin;
}
/* Icon on left in buttons */
.btn-icon-left {
    margin-right: 10px;
}

/*
----------------------------
Progress dialog
----------------------------
*/
.progress-dialog-message {
    margin-left: 20px;
}
.progress-dialog-content {
    display: flex;
    align-items: center;
    min-width: 400px;
}

/*
----------------------------
Alerts
----------------------------
*/
#message-alert {
    display: block;
    width: 100%;
    margin-bottom: 5px;
    background-color: #ffa500;
    padding: 10px 10px 10px 10px;
    box-sizing: border-box;

    color: white;
    text-align: left;
    font-family: 'Daimler CS' !important, sans-serif;
    font-size: 0.8rem !important;
    text-decoration: none;
    white-space: pre-line;

    user-select: none;
    -o-user-select: none;
    -ms-user-select: none;
    -moz-user-select: -moz-none;
    -khtml-user-select: none;
    -webkit-user-select: none;
}

#message-alert-icon {
    background-color: inherit;
    fill: white;
    height: 0.9rem;
    margin-bottom: -2px;
    margin-right: 2px;
}

#message-alert-remove-btn {
    float: right;
    min-width: 24px;
    margin-top: -6px;
    padding: 0px 0px 1px 0px;

    color: white;
    text-align: center;
    font-family: 'Daimler CS' !important, sans-serif;;
    font-size: 1rem !important;
}

#message-alert-remove-btn:hover {
    border-radius: 50%;
}

/*
 ----------------------

 Fonts

 ----------------------
 */

@font-face {
    font-family: 'Material Icons';
    font-style: normal;
    font-weight: 400;
    src: url(./assets/fonts/MaterialIcons-Regular.eot); /* For IE6-8 */
}

@font-face {
    font-family: 'Daimler CS';
    font-style: normal;
    src: local('Daimler CS'), local('DaimlerCS'), url(./assets/fonts/DaimlerCS-Light-web.woff) format('woff'),
        url(./assets/fonts/DaimlerCS-Bold-web.woff) format('woff');
}

@font-face {
    font-family: 'Material Icons';
    font-style: normal;
    font-weight: 400;
    src: local('Material Icons'), local('MaterialIcons-Regular'),
        url(./assets/fonts/MaterialIcons-Regular.woff) format('woff'),
        url(./assets/fonts/MaterialIcons-Regular.ttf) format('truetype');
}

.material-icons {
    font-family: 'Material Icons', sans-serif;
    font-weight: normal;
    font-style: normal;
    font-size: 24px; /* Preferred icon size */
    display: inline-block;
    line-height: 1;
    text-transform: none;
    letter-spacing: normal;
    word-wrap: normal;
    white-space: nowrap;
    direction: ltr;

    /* Support for all WebKit browsers. */
    -webkit-font-smoothing: antialiased;
    /* Support for Safari and Chrome. */
    text-rendering: optimizeLegibility;

    /* Support for Firefox. */
    -moz-osx-font-smoothing: grayscale;

    /* Support for IE. */
    font-feature-settings: 'liga';
}
