/*
----------------------------
Filter: validity
Hacky solution: get items by order and replace the content with an image. This rules will only be applied if there are exactly 3 items in the filter. This is to differentiate from the logging filter.
----------------------------
*/

li.MuiButtonBase-root:first-child:nth-last-child(3) > div:nth-child(2) > span:nth-child(1) {
    display: none;
}

li.MuiButtonBase-root:first-child:nth-last-child(3) ~ li:nth-child(2) > div:nth-child(2) > span:nth-child(1) {
    display: none;
}

li.MuiButtonBase-root:first-child:nth-last-child(3) ~ li:nth-child(3) > div:nth-child(2) > span:nth-child(1) {
    display: none;
}

li.MuiButtonBase-root:first-child:nth-last-child(3) > div:nth-child(2)::after {
    vertical-align: middle;
    content: url(data:image/png;base64,);
}

li.MuiButtonBase-root:first-child:nth-last-child(3) ~ li:nth-child(2) > div:nth-child(2)::after {
    vertical-align: middle;
    content: url(data:image/png;base64,);
}

li.MuiButtonBase-root:first-child:nth-last-child(3) ~ li:nth-child(3) > div:nth-child(2)::after {
    vertical-align: middle;
    content: url(data:image/png;base64,);
}

/*
----------------------------
Table
----------------------------
*/
.certificate-icon {
    height: 24px;
    margin-right: 10px;
}

.certificate-integrity-error {
    color: red;
    font-size: xx-large;
}

.certificate-icon-subject-cell {
    display: inline-flex;
    width: 170px;
    line-height: 26px;
}
.certificates-toolbar {
    max-width: 75vw; /* Make buttons "responsive" (CEBAS-1689) */
    text-align: left;
    margin: 10px 0 0 0;
}
.certificates-toolbar .certificates-toolbar-button {
    min-width: 120px;
    margin-right: 7px;
    margin-top: 2px;
    margin-bottom: 2px;
    text-align: center;
    padding-left: 10px;
    padding-right: 10px;
}

#certificates-update-btn:first-child:after,
#certificates-integrity-btn#certificates-integrity-btn:after {
    margin-left: 10px;
    content: '▾';
}
#certificates-update-menu li,
#certificates-integrity-menu li {
    font-size: 0.875em;
}
#import-result-summary {
    min-height: 50px;
    padding-bottom: 0px;
}

#certificate-table .MuiTable-root {
    padding: 5px 5px 5px 5px;
}

#certificate-table td.MuiTableCell-root.MuiTableCell-body,
#logging-table td.MuiTableCell-root.MuiTableCell-body {
    padding: 3px 5px 3px 5px;
}

#certificate-table label + .MuiInput-formControl,
#logging-table label + .MuiInput-formControl {
    margin-top: 2px !important;
}

#certificate-table .MuiIconButton-root {
    padding: 5px !important;
}

#certificate-table .MuiFormControl-root {
    width: 100%;
}

/*
----------------------------
Certificate context menu
----------------------------
*/
.certificate-context-menu-item-border {
    width: 150px;
    justify-content: flex-start !important;
    border-bottom: 1px solid #c8c8c8 !important;
}
.certificate-context-menu-item {
    justify-content: flex-start !important;
    width: 150px;
}
.certificate-context-menu-item-border *,
.certificate-context-menu-item * {
    vertical-align: middle;
    line-height: 17px;
}
.certificate-context-menu-item-border svg,
.certificate-context-menu-item svg {
    margin-right: 10px;
}

/*
----------------------------
Export certificate
----------------------------
*/
.export-certificate-table-value {
    font-size: 0.9rem;
    height: 30px;
    vertical-align: top;
}
#export-certificate-table td {
    width: 250px;
}
.export-certificate-value-container {
    margin-top: 20px;
}

/*
----------------------------
Details
----------------------------
*/
#certificate-details-error {
    margin: 5px 10px 10px 10px;
    overflow: hidden;
    color: red;
}

#certificate-details {
    margin-top: 5px;
    border: 1px solid #b5bcc7;
    overflow: hidden;
}
#certificate-details-content {
    display: block;
    margin: 5px 10px 10px 10px;
    padding-top: 10px;
    padding-bottom: 10px;
    overflow-x: auto;
}
#certificate-details-content table {
    table-layout: auto;
    border-spacing: 10px 0;
    border-collapse: separate;
    white-space: nowrap;
}
#certificate-details-content table td {
    padding: 0px 10px 0px 0px;
    font-size: 0.9rem;
    vertical-align: top;
}

/*
----------------------------
Installation Details
----------------------------
*/
#installation-details-content .MuiTable-root {
    table-layout: auto;
    word-wrap: break-word;
    word-break: break-all;
}

/*
----------------------------
Manual selection
----------------------------
*/
.manual-selection-dialog-tab-content {
    padding: 0 10px;
}

#manual-selection-dialog-content {
    height: 500px;
    overflow-y: visible;
}

#manual-selection-dialog .MuiPaper-root {
    overflow-y: auto;
    display: table;
}

#manual-selection-dialog {
    overflow-y: auto;
}

.certificate-selection {
    margin: 20px 0;
}
.cert-select-title {
    font-size: 0.85rem;
}
.cert-select {
    margin-top: 5px;
}
.cert-select-menu-heading-element {
    margin: 0 30px;
}

#selectable-table {
    user-select: none;
    -o-user-select: none;
    -ms-user-select: none;
    -moz-user-select: -moz-none;
    -khtml-user-select: none;
    -webkit-user-select: none;
}

#details-panel {
    user-select: text;
    -o-user-select: text;
    -ms-user-select: text;
    -moz-user-select: text;
    -khtml-user-select: text;
    -webkit-user-select: text;
}

/* div table for manual selection */
.zz-divtable-table {
    display: table;
    width: 100%;
    border: none;
}

.zz-divtable-scrollcontainer {
    display: block;
    overflow-y: scroll;
    flex: 1;
    max-height: 290px;
}

.zz-divtable-row,
.zz-divtable-elem__option {
    display: table-row !important;
}
.zz-divtable-heading {
    background-color: #eee;
    display: table-header-group;
    font-weight: bold;
}
.zz-divtable-cell,
.zz-divtable-head {
    border: none;
    display: table-cell;
    padding: 3px 10px;
}
.zz-divtable-body,
.zz-divtable-elem__menu-list {
    display: table-row-group;
}
