#btn-offline-login,
#btn-online-login {
    /* Workaround for centering items on IE (see CEBAS-1750). */
    height: 40px;
}

.logout-link a,
.cancel-logout-link {
    color: inherit;
    text-decoration: inherit;
}

#no-auto-logout-text {
    padding-left: 25px;
    color: red;
    font-family: 'Daimler CS', sans-serif;
    font-size: 0.875rem;
}

/*
----------------------------
(Offline) login
----------------------------
*/
.login-dialog-content,
.login-offline-dialog-content {
    min-width: 300px;
}
.login-textfield {
    display: block !important;
    width: 300px;
    height: 60px;
    margin-bottom: 15px !important;
}
.login-textfield div {
    width: 300px;
}
.login-dialog-error-message {
    width: 500px;
    min-height: 40px;
    color: red;
    margin-top: 15px;
}
.login-dialog-user {
    margin-bottom: 20px;
}

/*
----------------------------
Registration
----------------------------
*/
.registration-dialog-content {
    min-width: 300px;
}
.registration-textfield {
    display: block !important;
    width: 350px;
    height: 60px;
    margin-top: 15px !important;
}
.registration-textfield div {
    width: 350px;
}
.registration-textfield.second-password {
    margin-top: 30px !important;
}
.registration-dialog-user {
    text-align: center;
    margin-top: 20px;
    margin-bottom: 30px;
}

/*
----------------------------
ForgotPassword
----------------------------
*/
.btn-forgot-password-enabled {
    font-weight: bold;
    font-size: 0.9rem;
    color: #007a93;
    cursor: pointer;
}
.btn-forgot-password-disabled {
    font-size: 0.9rem;
    color: #676767;
}
#btn-forgot-message {
    font-size: 0.9rem;
    margin-left: 10px;
}
#navigation-btn-account:disabled {
    color: #9e9e9e;
}
#navigation-btn-account:first-child:after {
    margin-left: 10px;
    content: '▾';
}
