/*
----------------------------
Header
----------------------------
*/
.app-header {
    width: 100vw;
    display: flex;
    flex-direction: column;
    align-items: baseline;
    justify-content: center;
}
.app-header .logo-area {
    width: 100vw;
    padding-top: 1vmin;
    padding-bottom: 1vmin;
}
.app-logo {
    float: left;
    padding-left: calc(10vmin - 1px);
    margin-top: 1vh;
}
.header-info {
    float: right;
    padding-right: calc(10vmin - 1px);
}
.app-version {
    display: block;
    font-size: 0.9em;
    text-align: right;
}
.app-name {
    font-weight: bold;
    font-size: 1.7em;
}

.header-info a {
    color: black;
    text-decoration: none;
}

/*
----------------------------
Navigation: general
----------------------------
*/
.navigation {
    width: 100vw;
    margin: 0 0 1em 0;
    background-color: #000000;
}
.navigation button {
    min-width: 120px;
    color: #ffffff;
}
.navigation button:hover {
    color: #c8c8c8;
}
.navigation a {
    color: #ffffff;
    text-decoration: none;
}
.navigation .active button {
    background-color: #333333;
    font-weight: bold;
    cursor: default;
}
.navigation .active button:hover {
    color: #ffffff;
}

/*
----------------------------
Navigation: left
----------------------------
*/
.navigation-left {
    float: left;
    padding-left: 10vmin;
}

/*
----------------------------
Navigation: right
----------------------------
*/
.navigation-right {
    float: right;
    padding-right: 10vmin;
}
.navigation-right .user-handling {
    display: inline;
    font-size: 0.8em;
}
#navigation-btn-environment button {
    /** Overwrite min-width of navigation buttons with text */
    min-width: 70px;
}
.navigation-right .help-button {
    width: 25px;
}
.navigation-right .help-button:hover {
    color: #c8c8c8;
}
#navigation-menu li {
    font-size: 0.875rem;
}
