.configurations-content:after {
    content: '';
    display: table;
    clear: both;
}
.user-configurations,
.general-configurations {
    width: 35%;
    min-width: 610px;
    float: left;
}
.configurations-heading {
    font-weight: bold;
    font-size: 1.3em;
    margin-bottom: 15px;
}
.config-heading {
    font-size: 0.95em;
    font-weight: bold;
    margin-bottom: 10px;
}
.config-description {
    font-size: 0.9em;
}

.config-disable-desc {
    font-size: 0.9rem;
    color: #007a93;
    font-style: italic;
    display: block;
}

/*
----------------------------
SwitchConfigComponent
----------------------------
*/
.switch-config-component {
    max-width: 470px;
    margin-bottom: 20px;
}
.configurations-content .config-switch {
    /* To align switches they have to be moved to the left */
    margin-left: -10px;
    margin-right: 10px;
}

/*
----------------------------
Role priority component
----------------------------
*/
.role-priority-component {
    width: 470px;
    margin-top: 50px;
}
.roles-table {
    width: 380px;
    float: left;
    min-height: 376px;
}
.move-buttons {
    padding-top: 150px;
}
.move-buttons > button {
    margin: 10px 0 10px 20px;
}
.role-priority-component .config-description {
    margin-bottom: 20px;
}

/*
----------------------------
Proxy component
----------------------------
*/
.config-proxy-container {
    width: 450px;
    margin-bottom: 50px;
}
.config-proxy-paper {
    padding: 10px;
}
.config-proxy-paper-container {
    padding: 10px 0 10px 10px;
}
.config-proxy-type-group {
    display: block !important;
}
.config-proxy-protocol {
    font-size: 0.9rem;
    line-height: 58px;
    vertical-align: bottom;
    margin-right: 15px;
}
.config-proxy-input-container {
    min-height: 64px;
}
.config-proxy-input-container > div > label,
.config-proxy-input-container > div > div {
    font-size: 0.9rem;
}
.config-proxy-port-txt {
    width: 100px;
    margin-left: 15px !important;
}
.config-proxy-type-group > label {
    margin-right: 60px;
}
.config-proxy-type-group label > span:first-child {
    /* .config-proxy-type-group span[class*='jss'] { */
    padding: 12px 5px 12px 0;
}

/*
----------------------------
Accounts component
----------------------------
*/
.accounts-table {
    width: 450px;
    margin-bottom: 30px;
}
